<template>
    <div class="housecarousel">
        <center>
        <img id="mainimage" :src="images[currentIndex]">
        </center>

        <div>
            <img 
                class="preview" 
                v-for="(image, index) in images" 
                :key=index 
                :style="{width: imageWidth}" 
                :src="image" 
                @click="select(index)"/>
        </div>
    </div>
</template>

<script>
export default {
    name: "HouseCarousel",
    props: {
        id: Number
    },
    data() {
        return {
            images: [
                "img/ansicht1.jpg",
                "img/ansicht2.jpg",
                "img/ansicht3.jpg",
                "img/ansicht4.jpg",
                "img/ansicht5.jpg",
                "img/ansicht6.jpg",
                "img/ansicht7.jpg",
                "img/ansicht8.jpg",
                "img/ansicht9.jpg",
            ],
            currentIndex: 0,
        }
    },
    computed: {
        imageWidth() {
            return (Math.floor(100.0 / this.images.length)).toString() + "%";
        }
    },
    methods: {
        select(index) {
            this.currentIndex = index;
        }
    }
}
</script>

<style scoped>

#mainimage {
    height: 450px;
}

.preview {
    cursor:pointer;
}

</style>
