<template>
    <div class="housing">
        <h3>Wohnungsbau</h3>

        <table id="housingprojects">
            <tr class="item" v-for="item in items" :key="item.id" @click="viewProject(item.id)">
                <td>{{ item.type }}</td>
                <td>{{ item.status }}</td>
            </tr>
        </table>

        <house-carousel v-if="currentView !== -1" :id="currentView" />
    </div>
</template>

<script>
import HouseCarousel from "./HouseCarousel.vue";

export default {
    name: "HousingComponent",
    data() {
        return {
            items: [
                { type: "Einfamilienhaus", status: "Umbau", id: 0 },
                { type: "Mehrfamilienhaus", status: "Neubau", id: 1 },
                { type: "Einfamilienhaus", status: "Wohnraumerweiterung", id: 2 },
            ],
            currentView: -1
        }
    },
    methods: {
        viewProject(id) {
            this.currentView = id;
        }
    },
    components: {
        HouseCarousel
    }
}
</script>

<style scoped>
#housingprojects {
    padding-left: 60px;
}

.item {
    cursor: pointer;
}
</style>
